import React, { Component } from 'react';
import Card from './recommendation/Card';

class Recommendation extends Component {
  constructor() {
    super();

    this.handleTouchStart = this.handleTouchStart.bind(this);
  }
  handleTouchStart = () => {
    return;
  };

  render() {
    const cards = [
      {
        id: 0,
        title: 'CHICKEN KATSU UDON',
        description:
          'Home made chicken cultlet with Japanese style noodle soup',
        link: '/menu/udon/',
        linkTitle: 'Go to Udon',
        linkValue: 'UDON',
        url: '/images/menu/udon/chicken_katsu_udon.jpg'
      },
      {
        id: 1,
        title: 'RED DRAGON ROLL',
        description:
          '(cucumber, crab, prawn tempura with Teri & SP mayo sauce and torched, SP tuna & crispy flakes on top)',
        link: '/menu/kawa-special-roll/',
        linkTitle: 'Go to Kawa Special Roll',
        linkValue: 'KAWA SPECIAL ROLL',
        url: '/images/menu/kawa_special_roll/red_dragon_roll.jpg'
      },
      {
        id: 2,
        title: 'TUNA TATAKI ROLL',
        description:
          '(crab, cucumber, prawn tempura, avocado & seared tuna and crispy garlic on top with ponzu sauce)',
        link: '/menu/kawa-special-roll/',
        linkTitle: 'Go to Kawa Special Roll',
        linkValue: 'KAWA SPECIAL ROLL',
        url: '/images/menu/kawa_special_roll/tuna_tataki_roll.jpg'
      },
      {
        id: 3,
        title: 'RAICHU ROLL',
        description:
          '(crab, cucumber, prawn tempura, avocado, cream cheese & yam crunch on top with Teri sauce and SP mayo, wrapped with egg paper)',
        link: '/menu/kawa-special-roll/',
        linkTitle: 'Go to Kawa Special Roll',
        linkValue: 'KAWA SPECIAL ROLL',
        url: '/images/menu/kawa_special_roll/raichu_roll.jpg'
      },
      {
        id: 4,
        title: 'CHICKEN KATSU COMBO',
        description:
          'Home made chicken cultlet, sauce, rice, miso soup & salad',
        link: '/menu/kawa-special-roll/',
        linkTitle: 'Go to Katsu',
        linkValue: 'KATSU COMBO',
        url: '/images/menu/katsu/chicken_katsu.jpg'
      },
      {
        id: 5,
        title: 'POPCORN ROLL',
        description:
          '(deep fried scallop, cucumber, avocado, prawn tempura, crab, yam tempura mix on top with unagi and aioli sauce)',
        link: '/menu/kawa-special-roll/',
        linkTitle: 'Go to Kawa Special Roll',
        linkValue: 'KAWA SPECIAL ROLL',
        url: '/images/menu/kawa_special_roll/popcorn_roll.jpg'
      }
    ];

    return (
      <div className="kawa-container recommendation">
        {/* <div className="horizontal-background" /> */}
        <h1>
          Chef<span style={{ marginLeft: '8px' }}>'</span>s Recommendation
          <hr className="recommendation-hr" />
        </h1>
        <div className="r-cards">
          {cards.map((item, i) => (
            <Card key={i} property={item} />
          ))}
        </div>
      </div>
    );
  }
}

export default Recommendation;

